import { Entity } from '@/Domain/Entity'
import { IsInt } from 'class-validator'

export class Chart extends Entity {
  month = ''

  year: number = new Date().getFullYear()

  @IsInt()
  annuitet = 0

  @IsInt()
  loanSum = 0 // —Å—É–º–º–∞ –Ω–∞ –ø–æ–≥–∞—à–µ–Ω–∏–µ –æ—Å–Ω–æ–≤–Ω–æ–≥–æ –¥–æ–ª–≥–∞

  @IsInt()
  rateSum = 0 // —Å—É–º–º–∞ –Ω–∞ –ø–æ–≥–∞—à–µ–Ω–∏–µ –ø—Ä–æ—Ü–µ–Ω—Ç–æ–≤

  @IsInt()
  leftToPay = 0 // –æ—Å—Ç–∞–≤—à–∏–π—Å—è –¥–æ–ª–≥

  constructor (month: string, year: number, annuitet: number, rateSum: number, loanSum: number, leftToPay: number) {
    super()
    this.month = month
    this.year = year
    this.annuitet = annuitet
    this.rateSum = Math.round(rateSum)
    this.loanSum = Math.round(loanSum)
    this.leftToPay = Math.round(leftToPay)
  }
}
